.modalWindow {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    overflow: auto;
    background-color: rgba(231, 237, 240, 0.9);
    display: none; }

.container {
    margin: 25vh 25vw;
    width: 50vw;
    height: 50vh;
    box-sizing: border-box;
    padding: 15px;
    position: relative; }

.modalContent {
    display: none; }

.content-modal[data-items="1"] {
    .img-modal {
        background-image: url(../img/img4.png); } }

.content-modal[data-items="2"] {
    .img-modal {
        background-image: url(../img/img3.png); } }

.content-modal[data-items="3"] {
    color: #dddddd; }

.content-modal[data-items="4"] {
    .img-modal {
        background-image: url(../img/img1.png); } }

.content-modal[data-items="5"] {
    color: yellow; }

.content-modal[data-items="6"] {
    color: grey; }


.img-modal {
    background-image: url(../img/img2.png);
    height: 420px;
    background-position: center;
    background-repeat: center;
    background-size: cover; }
.slide-system-integration-hero {
    width: 420px;
    height: 420px;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    &__desc {
        width: 326px;
        text-align: center;
        color: #ffffff;
        font-family: 'GothamPro';
        font-size: 16px;
        font-weight: 400;
        line-height: 21px;
        margin-bottom: 34px;
        padding: 5px; } }

.pop-up-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(78, 100, 127, 0.7);
    border-radius: 50%; }

.slide-system-integration-hero__text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 326px; }

.slide-system-integration-hero__btn {
    border-radius: 14px;
    border: 1px solid #ffffff;
    background-color: transparent;
    padding: 6px 23px;
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.7px;
    display: block;
    margin: 0 auto;
    cursor: pointer;
    transition: 0.3s; }

.slide-system-integration-hero__btn:hover {
    color: #396f8c;
    background-color: #ffffff; }

.close-button-modal {
    background-color: #f58220;
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    transition: 0.3s;
    position: absolute;
    top: -60px;
    right: 0px;
    cursor: pointer; }

.close-button-modal img {
    display: block;
    width: 19px;
    height: 19px; }

.close-button-modal:hover {
    background-color: #2c5b72; }

