.modalLogin {
    background-color: rgba(44, 91, 114, 0.7);
    z-index: 1; }


.login-popUp {
    position: relative;
    max-width: 420px;
    margin: 0 auto;
    padding: 42px;
    border-radius: 30px;
    background-color: #ffffff;
    &__title {
        color: #2c5b72;
        font-family: "GothamPro";
        font-size: 18px;
        font-weight: 500;
        margin-bottom: 26px; } }

.login-form__text {
    width: 100%;
    border-radius: 24px;
    background-color: #e5edf1;
    font-size: 14px;
    padding: 16px 0 16px 25px;
    margin-bottom: 23px; }

::-webkit-input-placeholder {
    color: #396f8c;
    font-family: "GothamPro";
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.28px; }

.login-form__btn {
    font-family: "GothamPro";
    border-radius: 24px;
    text-align: center;
    background-color: #ee8a00;
    text-decoration: none;
    color: #ffffff;
    font-size: 16px;
    letter-spacing: 0.8px;
    width: 160px;
    padding: 13px 0;
    margin-top: 5px;
    transition: 0.3s; }

.login-form__btn:hover {
    background-color: #2c5b72; }

.close-btn {
    background-color: #f58220;
    width: 43px;
    height: 43px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    transition: 0.3s;
    position: absolute;
    top: -17px;
    right: -19px; }

.close-btn img {
    display: block;
    width: 13px;
    height: 14px; }

.close-btn:hover {
    background-color: #2c5b72; }

.slide-system-integration-hero__title {
    color: #f58220;
    font-family: "GothamPro";
